
.movies {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-wrap: wrap;
    padding: 50px;
    padding-top: 70px;
    width: 80%;
  
  }
  .movies .movie {
    width: 45%;
    background-color: white;
    margin-bottom: 70px;
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    font-weight: 300;
    padding: 20px;
    color: #adaeb9;
    box-shadow: 0 13px 27px -5px rgba(50,50,93,0.25), 0 8px 16px -8px rgba(0,0,0,0.3), 0 -6px 16px -6px rgba(0,0,0,0.025);
  }
  .movies img {
    position: relative;
    top: -50px;
    width: 130px;
  
    margin-right: 30px;
    box-shadow: 0 30px 60px -12px rgba(50,50,93,0.25), 0 18px 36px -18px rgba(0,0,0,0.3), 0 -12px 36px -8px rgba(0,0,0,0.025);
  }
  
  .movie .movie__title,
  .movie .movie__year {
    margin: 0;
    font-weight: 300;
  }
  .movie .movie__title {
    margin-bottom: 5px;
    font-size: 24px;
    color: #2f2f2f;
  }
  .movie .movie__genres {
    list-style: none;
    padding: 0;
    margin: 5px;
    display: flex;
    font-size: 14px;
    width : 10%;
  }
  .movie li {
    margin-right : 10px;
  }
  